import React from 'react';
import './Login.scss'
import logo from '../../assets/images/logo.png';
//./assets/images/logo.png
function LoginPage(props: any) {
    return (
        <div>
            Login Page here
            <div className="login-container">
                <img src={logo}/>
                {props.logoSrc && (
                    <img src={props.logoSrc}/>
                )}
                
            </div>
        </div>
    );
}

export default LoginPage;